import Head from "next/head";
import type { NextPage } from "next";
import React, { FormEvent, useState } from "react";

import SongSearch, { Track } from "../components/SongSearch";

const HomePage: NextPage = () => {
  const [selectedTrack, setSelectedTrack] = useState<Track | null>(null);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [submitting, setSubmitting] = useState(false);
  const [submitError, setSubmitError] = useState<string | null>(null);
  const [submitSuccess, setSubmitSuccess] = useState<string | null>(null);

  const handleSelect = (track: Track) => {
    setSelectedTrack(track);
    setSubmitError(null);
    setSubmitSuccess(null);
  };

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    if (!selectedTrack) {
      return;
    }

    setSubmitError(null);
    setSubmitSuccess(null);
    setSubmitting(true);

    try {
      const response = await fetch("/api/wish", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name,
          email,
          track: selectedTrack,
        }),
      });

      const data: { success?: boolean; message?: string; error?: string } =
        await response.json().catch(() => ({}));

      if (!response.ok || !data.success) {
        throw new Error(
          data.error ?? "Dein Songwunsch konnte nicht gespeichert werden."
        );
      }

      setSubmitSuccess(data.message ?? "Dein Songwunsch wurde gespeichert.");
    } catch (error) {
      console.error("Wish submission failed", error);
      setSubmitError(
        "Beim Speichern deines Songwunschs ist ein Fehler aufgetreten."
      );
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <>
      <Head>
        <title>Anthem Party – Songwunsch</title>
        <meta
          name="description"
          content="Such deinen Lieblingssong, wähle ihn aus und hilf mit, die Primetime Playlist zu gestalten."
        />
      </Head>
      <main className="mx-auto flex min-h-screen max-w-xl flex-col gap-8 px-4 py-10">
        {/* Title / Hero section */}
        <section className="w-full">
          <div className="mx-auto max-w-xl">
            <div className="overflow-hidden rounded-lg bg-gradient-to-r from-indigo-600 to-indigo-500 p-6 shadow-md">
              <h1 className="text-3xl font-extrabold leading-tight text-white">
                Anthem Party – Songwunsch
              </h1>
              <p className="mt-3 max-w-prose text-lg text-indigo-100">
                Suche deinen Lieblingssong, wähle ihn aus und steigere die
                Chancen, dass er zur Primetime gespielt wird.
              </p>
            </div>
          </div>
        </section>

        {/* Form appears above the song list so it's immediately visible */}
        <section className="rounded-lg border border-gray-100 bg-white p-6 shadow-lg">
          <h2 className="mb-3 text-xl font-semibold text-gray-900">
            Songwunsch absenden
          </h2>

          {!selectedTrack ? (
            <div className="flex flex-col gap-2">
              <p className="text-sm text-gray-600">
                Wähle zuerst einen Song aus der Liste, um ihn zu speichern.
              </p>
              <div className="rounded border border-dashed border-gray-200 bg-gray-50 p-4 text-sm text-gray-500">
                Kein Song ausgewählt
              </div>
            </div>
          ) : (
            <div className="flex flex-col gap-4">
              <div className="flex items-start gap-4">
                {selectedTrack.image_url ? (
                  // eslint-disable-next-line @next/next/no-img-element
                  <img
                    src={selectedTrack.image_url}
                    alt={selectedTrack.name}
                    className="h-20 w-20 rounded object-cover"
                  />
                ) : (
                  <div className="flex h-20 w-20 items-center justify-center rounded bg-gray-200 text-xs text-gray-600">
                    Kein Bild
                  </div>
                )}

                <div className="flex flex-col">
                  <span className="text-lg font-semibold text-gray-900">
                    {selectedTrack.name}
                  </span>
                  <span className="text-sm text-gray-600">
                    {selectedTrack.artists}
                  </span>
                  {selectedTrack.album && (
                    <span className="text-xs uppercase tracking-wide text-gray-400">
                      Album: {selectedTrack.album}
                    </span>
                  )}
                </div>
              </div>

              <form onSubmit={handleSubmit} className="flex flex-col gap-3">
                <label className="flex flex-col gap-1 text-sm text-gray-700">
                  Name oder Nickname
                  <input
                    type="text"
                    value={name}
                    onChange={(event) => setName(event.target.value)}
                    className="rounded border border-gray-200 bg-white px-3 py-2 text-sm text-gray-900 placeholder-gray-500 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-100 focus:outline-none"
                    placeholder="Name oder Nickname"
                    required
                  />
                </label>

                <label className="flex flex-col gap-1 text-sm text-gray-700">
                  E-Mail
                  <input
                    type="email"
                    value={email}
                    onChange={(event) => setEmail(event.target.value)}
                    className="rounded border border-gray-200 bg-white px-3 py-2 text-sm text-gray-900 placeholder-gray-500 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-100 focus:outline-none"
                    placeholder="name@beispiel.de"
                    required
                  />
                </label>

                <button
                  type="submit"
                  disabled={submitting}
                  className="rounded bg-green-600 px-4 py-2 text-sm font-medium text-white transition hover:bg-green-500 disabled:cursor-not-allowed disabled:opacity-70"
                >
                  {submitting ? "Speichern..." : "Songwunsch speichern"}
                </button>

                {submitError && (
                  <p className="text-sm text-red-600">{submitError}</p>
                )}
                {submitSuccess && (
                  <p className="text-sm text-green-600">{submitSuccess}</p>
                )}
              </form>
            </div>
          )}
        </section>

        {/* Song search / list */}
        <section className="rounded-lg border border-gray-100 bg-white p-6 shadow-sm">
          <SongSearch
            onSelect={handleSelect}
            selectedId={selectedTrack?.id ?? null}
          />
        </section>
      </main>
    </>
  );
};

export default HomePage;
